    line-height: 16px;
    font-weight: 600;
    color: #2d383f;
  }
  .header__nav-button {
    position: relative;
    display: flex;
    order: 0;
    border: none;
    background-color: inherit;
    margin-right: auto;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .header__nav-button::before {
    position: absolute;
    content: "";
    background-image: url("../img/svg/burger.svg");
    width: 26px;
    height: 19px;
    background-repeat: no-repeat;
    left: 0;
    top: 0;
  }
  .header__nav-button::after {
    position: absolute;
    content: "";
    background-image: url("../img/svg/burger-close.svg");
    width: 26px;
    height: 19px;
    background-repeat: no-repeat;
    background-position: center;
    left: -2px;
  }
  .header__nav-button span {
    display: none;
  }
  .header__nav-button:hover {
    opacity: 0.8;
  }
  .header__nav-button:focus {
    outline: none;
    opacity: 0.6;
  }
  .header__nav-button:active {
    opacity: 0.4;
  }
  .header__nav-button:disabled {
    opacity: 0.2;
    cursor: none;
  }
  .header__nav--nojs + .header__button {
    display: none;
  }
  .header__nav--nojs .header__nav-list {
    display: flex;
  }
  .header__nav--closed .header__nav-list {
    display: none;
  }
  .header__nav--opened .header__nav-list {
    display: flex;
  }
  .header__nav--closed ~ .header__nav-button::after {
    display: none;
  }
  .header__nav--opened ~ .header__nav-button::before {
    display: none;
  }
  .header__nav--opened ~ .header__nav-button::after {
    display: block;
  }
  .header__nav--opened::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    top: 0;
    left: 0;
  }
  .header__nav--opened ~ .header__logo {
    color: #2d383f;
    z-index: 2;
  }
  .header__nav--opened ~ .header__logo:hover {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__logo:focus {
    outline: none;
    color: #1c3374;
  }
  .header__nav--opened ~ .header__logo:active {
    color: #0266c1;
  }
  .header__nav--opened ~ .header__contact {
    color: #2d383f;
    z-index: 2;
  }
  .header__nav--opened ~ .header__contact:hover {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__contact:focus {
    outline: none;
    color: #1c3374;
  }
  .header__nav--opened ~ .header__contact:active {
    color: #0266c1;
  }
}
.menu--opened {
  position: relative;
  overflow: hidden;
}
.menu--opened::before {
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 2;
}

@media (max-width: 767px) {
  .header__wrapper {
    display: grid;
    width: 100%;
    padding: 15px 0 40px;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "button logo" "button contact";
  }
  .header__logo {
    color: #ffffff;
    margin: 0;
    margin-left: auto;
    order: 1;
    grid-area: logo;
    padding: 0;
  }
  .header__logo svg {
    width: 112px;
    height: 34px;
